import React from "react";
import "./ManagerPanel.css";
import { calculateTotalAmount } from "./ManagerPanelFunctions";
import jsPDF from "jspdf";

const ManagerPanel = ({ tables = [], wynosTables = [], onClose }) => {
	const servedTables = JSON.parse(localStorage.getItem("servedTables")) || [];
	const servedWynosTables =
		JSON.parse(localStorage.getItem("servedWynosTables")) || [];

	const totalTablesDuringDay = servedTables.length;
	const totalWynos = servedWynosTables.length;
	const totalAmount = calculateTotalAmount(tables, wynosTables);

	const handleExportToPDF = () => {
		const doc = new jsPDF();
		const currentDate = new Date();
		const dateString = `${currentDate.getDate()}-${
			currentDate.getMonth() + 1
		}-${currentDate.getFullYear()}`;

		doc.text("Panel Managera", 20, 10);
		doc.text(
			`czna liczba stolikw (w cigu dnia): ${totalTablesDuringDay}`,
			20,
			20
		);
		doc.text(`czna liczba wynosw: ${totalWynos}`, 20, 30);
		doc.text(`czna kwota: ${totalAmount} PLN`, 20, 40);

		doc.save(`panel_managera_${dateString}.pdf`);

		localStorage.clear();
		window.location.reload();
	};

	return (
		<div className="manager-panel">
			<button className="close-button" onClick={onClose}>
				X
			</button>
			<h2>Panel Managera</h2>
			<div className="manager-info">
				<div className="info-item">
					<strong>czna liczba stolikw (w cigu dnia):</strong>{" "}
					{totalTablesDuringDay}
				</div>
				<div className="info-item">
					<strong>czna liczba stolikw i wynosw (opaconych):</strong>{" "}
					{totalWynos}
				</div>
				<div className="info-item">
					<strong>czna kwota:</strong> {totalAmount} PLN
				</div>
			</div>
			<button className="export-button" onClick={handleExportToPDF}>
				Zamknij dzie i pobierz PDF
			</button>
		</div>
	);
};

export default ManagerPanel;
